
/**
 *
 * _header
 *
 */

.header{
	position: fixed;
	top: 1em;
	width: 100%;
	color: @red;
	z-index: 999;
	font-family: @headings-font-family;
	transition: top 500ms ease, background 500ms ease;

	& > .container{
		position: relative;
	}
	&.scrolled-view{
		top: 0;
		height: 50px;
    	background-color: rgba(133, 24, 38, 0.8);

    	@media @min-sm{
    		top: 2em;
    		height: auto;
    		background-color: transparent;
    	}
	}

	@media @min-sm{
		top: 2em;
		// position: fixed;
	}

}



.header__logo{
	position: absolute;
	z-index: 2;
  left: 1em;
	display: inline-block;
	transition: transform 500ms ease;
	path{
		fill: white;
	}

	.scrolled-view & {
		transform: scale(.8);
	}

	a{
		display: block;
	}
}

.nav-links{
	.clearfix();
  margin: 0;
  padding: 0;
  list-style: none;


  li{
   	float: left;
  }

}

.nav-links--header .active{
  	a{
  		background-color: @gold-dark;
  	}
}

.header__nav{
	position: absolute;
	right: 1em;
	// opacity: .0;

	&:after{
		content: '';
		position: absolute;
		width: 300%;
		height: 50px;
		top:0;
		left: 100%;
		background-color: @black;
		display: block;
	}
}

.nav-brand__text{
	position: absolute;
	top: .75em;
	left: 4.5em;
	color: @red-light;
	max-width: 4em;

	h6{
		margin: 0;
		margin-top: 10px;
	}

	@media @min-sm{
		top: 1.5em;
		left: -.5em;
	}
}


.nav-links--header{
  display: inline-block;
  background: @black;
	position: relative;
	z-index: 3;
	transition: all 750ms @trans;
	transform: translateX(110%);
	// opacity: 0;

	@media @min-sm{
		transform: translateX(0%);
		opacity: 1;
	}

  a{
		.btn();
		// .button-variant(white; transparent; white);
    color: white;
		font-size: @font-size-h5;
		padding: 1em;

		&:hover, &:focus{
			color: white;
			background-color: @gold-dark;
		}
  }
	li{
		transform: scaleY(0);
		transition: transform 500ms ease;

		@media @min-sm{
			transform: scaleY(1);
		}
	}

	.scrolled-view &{
		transform: translateX(110%);

		li{
			transform: scaleY(0);
		}
	}
}

