.mobile-nav{
	position: absolute;
	top: 0;
	right: -3px;
	max-height: 50px;
  overflow: hidden;
	z-index: 2;
	background-color: @black;
	text-align: center;
	padding-right: 1em;
	transform: translateX(0%);
	opacity: 1;
	transition: opacity 750ms @trans, transform 750ms @trans;

	.scrolled-view &{
		svg{
			opacity: 1;
			transform: scale(1);
			transition: opacity 500ms ease 550ms, transform 500ms ease 550ms;
		}
	}
}

.mobile-btn{
	min-height: 50px;
	min-width: 50px;
	display: block;
	overflow: hidden;

	&.is-active{
		svg{
			margin-top: 15px;
		}
		.mobile-btn__close{
			display: block;
		}
		circle{
			display: none;
		}
	}

	&:hover, &:focus{
		color: white;
		background-color: @gold-dark;

		circle{
			transform: translateY(-5px);
		}
	}

	svg{
		display: inline-block;;
		opacity: 1;
		transform: scale(1);
		transition: opacity 500ms ease 550ms, transform 500ms ease 550ms;

		@media @min-sm{
			opacity: 0;
			transform: scale(.75);
			transition-delay: 0ms;
		}
	}

	circle{
		transition: transform 500ms ease;

		&:nth-child(1){
			transition-delay: 0ms;
		}
		&:nth-child(2){
			transition-delay: 150ms;
		}
		&:nth-child(3){
			transition-delay: 300ms;
		}
	}
}

.mobile-btn__close{
	display: none;
}
